<template>
  <div id="random" class="scrollTouch" :class="{hasHeader: $utils.showNavBar()}">
    <headerNav class="headerNav" v-if="$utils.showNavBar()"></headerNav>
    <div class="top">
      <div class="banner iconSize">
        <!-- <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/share_page2.png" alt="图片"> -->
        <img :src="$utils.setImageSize(shareCarInfo.thumb, 500)" alt="">
      </div>
      <div class="userInfo flex-row">
        <div class="avatar iconSize">
          <img v-if="userInfo.nickname" :src="userInfo.avatar" alt="头像">
          <img v-else src="../../../assets/img/my_log.png" alt="logo">
        </div>
        <div class="nickname  flex-row f24">{{userInfo.nickname ? userInfo.nickname : '掘金宝'}}</div>
      </div>
      <div class="tip posC flex-row">和我一起看资讯影视<br>赚车子房子</div>
    </div>
    <!-- 内容部分由topBg和bottomBg拼接而成 -->
    <div class="content">
      <div class="topBg">
        <div class="button posC" @click="getMoney"></div>
        <div class="total_count text-center">
          今日已有<span class="redColor">{{shareCarInfo.total_count ? shareCarInfo.total_count : '' | setPeople}}</span>人成功领取到现金、豪车<br>赶紧试试
        </div>
        <swiper class="list swiper-no-swiping" v-if="list.length > 1" :options="swiperOptions" ref="swiper">
          <swiper-slide class="swiper-slide" v-for="(item, index) in list" :key="index">
            <div class="item flex-row jusB" v-for="(content, index1) in item" v-if="item" :key="index1">
              <div class="left flex-row">
                <div class="avatar iconSize">
                  <img :src="`${content.head}`" alt="头像" onerror='this.src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png"'>
                </div>
                <div class="nickname">
                  <div class="name f28">{{content.name}}</div>
                  <div class="create_time f24">{{content.minutes}}</div>
                </div>
              </div>
              <div class="right f28">领到了<span class="redColor">{{content.vcoin}}元</span></div>
            </div>
          </swiper-slide>
        </swiper>
      </div>
      <div class="bottomBg"></div>
    </div>
    <div class="flex-row logoInfo">
      <div class="logo iconSize">
        <img src="../../../assets/img/my_log.png" alt="logo">
      </div>
      掘金宝
    </div>
    <carShare v-if="showCarShare && $route.path === '/share_page/3'" @shareSuccess='shareSuccess' :appShare='appShare' :fatherParams='fatherParams'></carShare>
    <ExitConfirm v-if="showConfirm" :config='config' :carInfo='shareCarInfo' @closeConfirm='closeConfirm'></ExitConfirm>
  </div>
</template>
<script>
import headerNav from '@/components/task/header-nav'
import ExitConfirm from '@/components/share/share_page2/exit_tip'
import carShare from '@/components/share/carShare'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide,
    headerNav,
    ExitConfirm, // 退出确认弹框
    carShare // 汽车分享
  },
  data () {
    return {
      shareCarInfo: {},
      shareParams: {},
      swiperOptions: {
        loop: true,
        autoplay: {
          delay: 2000
        },
        direction: 'vertical'
      },
      appShare: {
        path: `/share_page/${Math.ceil(Math.random() * 3)}`
      },
      userInfo: {}, // 邀请人信息
      list: [], // 邀请人列表
      showConfirm: false,
      showCarShare: false,
      hasReward: true, // 默认设置为有奖励
      config: {
        title: '你的现金还没有领取，逾期将收回确定要退出吗？',
        cancelText: '放弃领取',
        confirmText: '继续领钱'
      }, // 组件文字配置
      fatherParams: {}
    }
  },
  // 在路由离开之前监听
  beforeRouteLeave (to, from, next) {
    if (to.path !== '/get_cash') {
      if (this.hasReward) {
        this.showConfirm = true
        next(false)
      } else {
        next()
      }
    } else {
      next()
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    // 分享成功
    shareSuccess () {

    },
    // 关闭弹窗
    closeConfirm ({action}) {
      if (action === 'confirm') {
        this.showConfirm = false
      } else if (action === 'cancel') {
        this.showConfirm = false
        this.hasReward = false
        this.$router.replace('/')
      }
    },
    // 跳转到url
    getMoney () {
      this.$router.push({
        path: '/get_cash',
        query: this.$route.query
      })
    },
    getInviteUser () {
      if (this.$route.query.inviteCode) {
        this.$api.get('USER_INVITEINFO', {invitation_code: this.$route.query.inviteCode}, res => {
          if (res.data) {
            this.userInfo = res.data
          }
        })
      }
    },
    // 获取随机数据
    getRandom () {
      this.$api.get('SHARE3_LOOP', {}, res => {
        let arr = []
        let count = 0
        let tempArr = []
        for (let v of res.data) {
          count++
          tempArr.push(v)
          if (count === 6) {
            arr.push(tempArr)
            count = 0
            tempArr = []
          }
        }
        if (count !== 6 && tempArr.length !== 0) {
          arr.push(tempArr)
          tempArr = []
        }
        this.list = arr
      })
    },
    // 获取车辆信息
    getCarInfo () {
      let data = {}
      if (this.$route.query.gid) {
        data.gid = this.$route.query.gid
        // 存在商品id时重新获取文案
        this.getFatherParams()
      }
      this.$api.get('SHARE_GUILDBROWERS', data, res => {
        this.shareCarInfo = res.data
        // 不存在id时当做分享文案
        if (!this.$route.query.gid) {
          this.fatherParams = {
            title: `180天就能免费赚豪车赚洋房？！${res.data.total_count}人为此彻夜未眠！`,
            desc: `过年将${res.data.brand_name}提回家，赶紧上车 >`,
            img_url: res.data.thumb,
            gid: res.data.id
          }
          this.appShare.path = this.$route.path
          this.showCarShare = true
        }
      })
    },
    // 获取随机文案
    getFatherParams () {
      this.$api.get('SHARE_GUILDBROWERS', {}, res => {
        this.fatherParams = {
          title: `180天就能免费赚豪车赚洋房？！${res.data.total_count}人为此彻夜未眠！`,
          desc: `过年将${res.data.brand_name}提回家，赶紧上车 >`,
          img_url: res.data.thumb,
          gid: res.data.id
        }
        this.appShare.path = this.$route.path
        this.showCarShare = true
      })
    },
    initData () {
      // 获取汽车信息
      this.getCarInfo()
      this.getRandom()
      this.getInviteUser()
    }
  },
  mounted () {
    this.initData()
  }
  
}
</script>
<style lang="scss" scoped>
#random{
  height: 100vh;
  box-sizing: border-box;
  overflow-y: auto;
  .top{
    height: 563px;
    position: relative;
    .userInfo{
      position: absolute;
      left: 30px;
      top: 20px;
      overflow: hidden;
      white-space: nowrap;
      .avatar {
        width: 60px;
        height: 60px;
        overflow: hidden;
        border-radius: 50%;
        margin-right: 10px;
      }
      .nickname {
        font-size: 24px;
        line-height: 34px;
        color: #fff;
      }
    }
    .tip {
      top: 80px;
      font-weight: 600;
      font-size: 36px;
      color: #E9B63C;
      white-space: nowrap;
      line-height: 50px;
    }
  }
  &.hasHeader {
    .content {
      top: 448px;
    }
  }
  .content{
    background: none;
    margin-top: -120px;
    left: 0;
    right: 0;
    .total_count{
      line-height: 40px;
      font-size: 28px;
      margin-bottom: 20px;
      .redColor{
        color: #f5a623;
      }
    }
    .topBg {
      position: relative;
      z-index: 2;
      left: 0;
      right: 0;
      height: calc(100vh - 450px);
      background: url('../../../assets/img/share/share_page3_bg.png') no-repeat left top transparent;
      background-size: 100% 862px;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 300px;
        background: #272727;
        z-index: -1;
      }
      .total_count {
        font-size: 28px;
        color: #F5F5F5;
        font-weight: 600;
        padding-top: 230px;
        line-height: 40px;
      }
    }
    .bottomBg {
      background: #272727;
      min-height: 300px;
      // margin-top: -20px;
    }
    .list{
      margin: 0 auto;
      width: 690px;
      background: #B61406;
      overflow: hidden;
      border-radius: 15px;
      position: relative;
      font-size: 30px;
      height: 720px;
      color: #fff;
      overflow: hidden;
      .swiper-slide{
        height: 720px;
      }
      .item {
        padding: 20px 15px;
        height: 80px;
        height: 120px;
        box-sizing: border-box;
        // border-bottom: 1px solid #dbdbdb;
        position: relative;
        background: #B61406;
        &::after{
          content: '';
          height: 1px;
          transform: scaleY(.5);
          background: #DADADA;
          position: absolute;
          bottom: 0;
          left: 16px;
          right: 16px;
        }
        &:last-child::after{
          height: 0;
        }
        .left{
          text-align: left;
          .avatar {
            width: 80px;
            height: 80px;
            border-radius: 50%;
            margin-right: 20px;
            overflow: hidden;
          }
          .create_time{
            font-size: 24px;
            padding: 10px 0 0;
          }
        }
        .right{
          font-weight: 600;
          .redColor {
            color: #f37f32;
          }
        }
      }
    }
    .button {
      top: 80px;
      width: 128px;
      height: 128px;
      background: url('../../../assets/img/share/share_page3_button.png') no-repeat center;
      background-size: cover;
    }
  }
  .logoInfo{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100px;
    box-sizing: border-box;
    background: #B61406;
    z-index: 3;
    font-size: 28px;
    color: #F5A623;
    .iconSize{
      width: 56px;
      height: 56px;
      overflow: hidden;
      border-radius: 50%;
      margin-right: 20px;
    }
  }
}
</style>



